.App {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    height: 100vh;
}

.searchBar {
    margin: 0.5em 0;
}
.mapsContainer {
    border-top: 1px solid lightgray;
    padding: 0.5em;
    display: flex;
    flex-direction: row;
    max-width: 1300px;
    flex-wrap: wrap;
    justify-content: center;
    overflow-y: auto;
}

.map {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-radius: 15px;
    margin: 0.25em;
}

.mapImg {
    width: 800px;
    height: 600px;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    cursor: pointer;
}

.mapImg.full {
    border-radius: 0px;
}

.mapImg.small {
    width: 200px;
    height: 150px;
}

.mapImg.medium {
    width: 400px;
    height: 300px;
}

.mapLabel {
    margin: 0.25em 0;
    font-weight: bold;
}

.mapDialog {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0 ,0.8);
}

.fullImgContainer {
    position: relative;
    display: flex;
    flex-direction: column;
}

button {
    border: 2px solid gray;
}
.closeBtn {
    position: absolute;
    right: 10px;
    top: 10px;
    border: none;
    color: white;
    font-weight: bold;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.5);
}